.footer {
  padding: 0 20px 33px;
  background-color: $dark-bg-color;
  color: $white-color;

  @media screen and (min-width: 768px) {
    padding: 52px 80px 33px;
  }

  @media screen and (min-width: 1200px) {
    padding: 60px 80px 32px;
  }
}
.footer-list {
  margin-bottom: 33px;

  padding-top: 25px;
  // border-top: 1px solid $white-color;
  -moz-column-count: 2; /* Firefox */
  -webkit-column-count: 2; /* Safari and Chrome */
  column-count: 2;

  @media screen and (min-width: 1200px) {
    display: none;
    // -moz-column-count: 4; /* Firefox */
    // -webkit-column-count: 4; /* Safari and Chrome */
    // column-count: 4;
    // padding-left: 100px;
    // padding-right: 100px;
  }
}

.footer-list-item {
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
}

.footer-create-link {
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
}

.footer-create-box {
  display: flex;
  justify-content: space-between;
}
